import React , { Fragment, useEffect, useState} from 'react'
import './App.css'
import {InputGroup ,Input, }  from 'reactstrap'
import SpeechRecognition, { useSpeechRecognition } from 'react-speech-recognition'


import axios from 'axios'
import 'bootstrap/dist/css/bootstrap.min.css';

import { CancionesList } from './components/CancionesList'

export function App() {

  const{
    transcript,
  resetTranscript,
} = useSpeechRecognition();


  const [grabando, setGrabando] = useState(false)
  const [title, setTitle] = useState('')
  const [carga, setCarga] = useState(false)
  const [vacio, setVacio] = useState(false)
  const [inicio, setInicio] = useState(false)
  const [canciones, setCanciones] = useState([]);

const guardarTitulo = ({target}) => {
  
  setTitle(target.value)
}
    
const url= 'https://shazam.p.rapidapi.com/search';

const fetchApiVoz = async () => {
  try {
  const res = await axios.get(url, {params: {term: transcript+'', locale: 'en-US', offset: '0', limit: '15'},headers: {
    'X-RapidAPI-Key': '83ad539028mshc0d1d05fc06b696p170448jsnda3c87df7b92', 'X-RapidAPI-Host': 'shazam.p.rapidapi.com'}})
    setCanciones(res.data.tracks.hits)
    setCarga(false)
  } catch (error) {
    console.log(error)
    setCarga(false)
    setVacio(true)
    setInicio(false)
    setCanciones([])
    resetTranscript()
  }
   
    
}

const fetchApi = async () => {
  try {
  const res = await axios.get(url, {params: {term: title+'', locale: 'en-US', offset: '0', limit: '15'},headers: {
    'X-RapidAPI-Key': '83ad539028mshc0d1d05fc06b696p170448jsnda3c87df7b92', 'X-RapidAPI-Host': 'shazam.p.rapidapi.com'}})
    setCanciones(res.data.tracks.hits)
    setCarga(false)
  } catch (error) {
    console.log(error)
    setCarga(false)
    setVacio(true)
    setInicio(false)
    setCanciones([])
  }
   
    
}

const vacioHtml = () => {
  if(vacio){
    return(
      <div className='row justify-content-center'>
        <h2 className='noRes'>No se encontro resultados, intenta otra vez.</h2>
      </div>
    )
  }
}

  



const guia =()  => {
  if(!inicio){
  return (
    <div className='usoPasosRow'>
    <div className='row'>
    <div className='col-1'></div>
    <div className='justify-content-center col-10 usoPasos'>
      <p className='uso col-12'>
        Como se usa
      </p>
      <p className='paso1 col-12'>
       Paso 1: Ingresa el nombre de una canción o un artista, <br/> tambien puedes introducir parte de la letra de la cancion.
      </p>
      <p className='paso2 col-12'>
       Paso 2: Presiona el boton buscar.
      </p>
      <p className='paso3 col-12'>
        Paso 3: Encuentra la cancion que deseas <br/> en los resultados de la busqueda.
      </p>
      <p className='paso4 col-12' >
        Paso 4: Presiona el boton para ver la cancion <br/> en youtube y spotify.
      </p>
    </div>
    <div className='col-1'></div>
    </div>
    </div>
  )}
  else{
    return null
  }
  
}

    

const transcripcion = () => {
  
  if(!grabando){
    document.getElementById('bv').setAttribute('id', 'bvg')
   
    //cambiar texto de boton
    document.getElementById('bvg').innerHTML = 'Grabando...'
   
    SpeechRecognition.startListening()
    setGrabando(true)
  }
  if(grabando){
    document.getElementById('bvg').setAttribute('id', 'bv')
    document.getElementById('bv').innerHTML = 'Voz'
 SpeechRecognition.stopListening()
  document.getElementById('titulo').value = transcript
 buscarVoz()
  setGrabando(false)
  }
  
}



const buscarVoz = () => {
  setTitle(transcript)
  setCarga(true)
  setVacio(false)
  setInicio(true)
  fetchApiVoz()
}

const buscar = () => {
  
  setCarga(true)
  setVacio(false)
  setInicio(true)
  fetchApi()

}
useEffect(() => {
  // fetchApi()
  
} , [])


  return (
    <Fragment >
      <InputGroup className='ip'>
        
      <div align="center" className='buscador row justify-content-center'
      >
        <h1 className='titulo' 
        >
        Detext music
        </h1>
       
      <div className='col-8 col-md-6 col-lg-8 col-sx-6' >
    <Input 
    id='titulo' disabled={carga}
     type='text' placeholder="Canción, artista o letra" text={title} onChange={guardarTitulo} 
        />
        </div>
        <div className='col-4 col-lg-1'>
    <button className='boton' disabled={carga} onClick={buscar} >
      Buscar
    </button>
    </div>
    <div className='col-4 col-lg-1'>
    
    <button className='boton btg'  disabled={carga} onClick={transcripcion}
    id='bv'>
      Voz
    </button>
   
    </div>
    </div>

  
  </InputGroup>
  <CancionesList listaCanciones={canciones} carg={carga} className=' row justify-content-center can' />
  {vacioHtml()}
    {guia()}
    
    
   </Fragment>
  )
}

