import React, { Fragment } from 'react'
import './CancionesItem.css' 
import {
    Modal,  ModalBody
} from "reactstrap"

export function CancionesItem({cancion}) {
    
    // Modal open state
    const [modal, setModal] = React.useState(false);
  
    // Toggle for Modal
    const toggle = () => setModal(!modal);
    
    
    
    
    const youtube ='https://www.youtube.com/results?search_query='+cancion.track.title+' '+ cancion.track.subtitle;
    const spotify ='https://open.spotify.com/search/'+cancion.track.title+' '+ cancion.track.subtitle;
    
  return (
    <Fragment>
        <tr>
    <td >
        {
            cancion.track.title
        }
    </td>
    <td>
        {
            cancion.track.subtitle
        }
    </td>
    <td>
      <img className='imgCancion'
       src={cancion.track.images.coverart} alt=""   onClick={toggle} />
       
    </td>
    <td>
        
        <a href={youtube} target="_blank" rel="noopener noreferrer"  > 
        <img  className='enlace'
        src={require('./img/youtube_icon-icons.com_66686.png')} alt="avance" />
        </a>
        
        <a href={spotify} target="_blank" rel="noopener noreferrer"  >  
        <img className='enlace'
        src={require('./img/Spotify_icon-icons.com_66783.png')} alt="avance" />
        </a > 
        
    </td>

    </tr>

    <Modal isOpen={modal}
                toggle={toggle}
                modalTransition={{ timeout: 0 }}
                centered='true'>
                <ModalBody className=' row justify-content-center' >
                <img className='imgCancion imgModal' 
       src={cancion.track.images.coverart} alt="" />
                </ModalBody>
            </Modal>


    </Fragment>
  )
}
